import React from 'react'
import styles from './About.module.css'
import { Paper } from '@material-ui/core'
import { useTranslate } from '../hooks/useTranslate'
import Location from '../../mobx/Location'

interface Info {
	header: string
	info: string
	queryId: string
}

export default function About() {
	const [aboutInfo, setAboutInfo] = React.useState<Info>()
	const translate = useTranslate({
		loading: { en: 'Loading info', ru: 'Загрузка информации' },
	})

	React.useEffect(() => {
		fetch(`https://shop.digiseller.ru/xml/shop_menu.asp?transp=cors&format=json&lang=${Location.getLanguage()}&seller_id=654077&menu=about`)
			.then(response => response.json())
			.then(response => {
				setAboutInfo(response)
			})
	}, [])

	if (!aboutInfo) return <>{translate.loading}...</>

	return (
		<Paper className={styles.container} elevation={0}>
			<p className={styles.header}>{aboutInfo.header}</p>
			<div dangerouslySetInnerHTML={{ __html: aboutInfo.info }} />
		</Paper>
	)
}
