import React from 'react'
import styles from './Main.module.css'
import { Grid } from '@material-ui/core'

export default function Navbar() {
	return (
		<div className={styles.container}>
			<img src="https://richlegion.ru/global/images/logo.svg" className={styles.logo} />
			<Grid container direction="column" justify="center" alignItems="flex-start" className={styles.text}>
				<p className={styles.head}>
					Добро пожаловать в интернет-магазин RICHLEGION
					<br />
					Здесь Вы можете купить золото World of Warcraft по низкой цене!
				</p>
				<p className={styles.body}>
					Lorem, ipsum dolor sit amet consectetur adipisicing elit. Corporis totam veniam non dolor ullam ipsum nesciunt vero adipisci.
					Magni deleniti voluptatibus architecto eligendi ab aliquid libero harum error, tempora cum! Ab nam amet quidem ratione, deleniti
					neque assumenda quaerat, in similique eveniet laudantium, culpa nemo dignissimos sit illo iure praesentium.
				</p>
			</Grid>
		</div>
	)
}
