import React from 'react'
import styles from './Contacts.module.css'
import { Paper } from '@material-ui/core'
import { useTranslate } from '../hooks/useTranslate'
import Location from '../../mobx/Location'

interface Info {
	header: string
	info: string
	queryId: string
}

export default function Contacts() {
	const [contactInfo, setContactInfo] = React.useState<Info>()
	const translate = useTranslate({
		loading: { en: 'Loading contactinfo', ru: 'Загрузка контактной информации' },
	})

	React.useEffect(() => {
		fetch(
			`https://shop.digiseller.ru/xml/shop_menu.asp?transp=cors&format=json&lang=${Location.getLanguage()}&seller_id=654077&menu=contacts`
		)
			.then(response => response.json())
			.then(response => {
				setContactInfo(response)
			})
	}, [])

	if (!contactInfo) return <>{translate.loading}...</>

	return (
		<Paper className={styles.container} elevation={0}>
			<p className={styles.header}>{contactInfo.header}</p>
			<div dangerouslySetInnerHTML={{ __html: contactInfo.info }} />
		</Paper>
	)
}
