import React from 'react'
import styles from './App.module.css'
import { HashRouter as Router, Switch, Route } from 'react-router-dom'
import ContentPattern from './components/ContentPattern/ContentPattern'
import NotFound from './components/NotFound/NotFound'
import Navbar from './components/Navbar/Navbar'
import Footer from './components/Footer/Footer'
import Main from './components/Main/Main'
import Shop from './components/Shop/Shop'
import Suppliers from './components/Suppliers/Suppliers'
import Contacts from './components/Contacts/Contacts'
import About from './components/About/About'
import Reviews from './components/Reviews/Reviews'
import Guarantee from './components/Guarantee/Guarantee'
import Payment from './components/Payment/Payment'
import Location from './mobx/Location'
import ModalInfo from './components/ModalInfo'

function CentralContainer() {
	return (
		<ContentPattern>
			<Switch>
				<Route exact path="/" component={Main} />
				<Route path="/shop" component={Shop} />
				{/* <Route path="/suppliers" component={Suppliers} /> */}
				<Route path="/contacts" component={Contacts} />
				<Route path="/about" component={About} />
				<Route path="/reviews" component={Reviews} />
				<Route path="/guarantee" component={Guarantee} />
				<Route path="/payment" component={Payment} />
				<Route component={NotFound} />
			</Switch>
		</ContentPattern>
	)
}

export default function App() {
	return (
		<Router>
			<ModalInfo />
			<div className={styles.container}>
				<Navbar />
				<CentralContainer />
			</div>
			<Footer />
			<iframe
				src={`https://chat.digiseller.ru/asp/chatWidget.asp?uid=B51F96D78E21400BA323693DE3B29A46&lang=${Location.getLanguage()}`}
				style={{ position: 'fixed', bottom: 0, right: 50, border: 0, width: 264, height: 42 }}
			/>
		</Router>
	)
}
